import { apiRequest } from "@/config/axios";
import cards from "@/config/cards.json";
import { API_URL } from "@/config/env";

// Calculate cards for the lobby/match
export function calcMaxCards(numberOfPlayers = 0, demandedCards = 15) {
    return new Promise((resolve, reject) => {
        try {
            const minCards = 12;
            const maxCards = 25;
            let cardsArr = [];
            const perDeck = 52;
            let totalDecks = 1;
            const requiredCards = numberOfPlayers * (demandedCards < minCards ? minCards : maxCards < demandedCards ? maxCards : demandedCards);

            let extraCards = 0;
            let removed = 0;

            while((totalDecks * perDeck) < requiredCards) {
                totalDecks++;
            }

            extraCards = (totalDecks * perDeck) - requiredCards;

            console.log("extraCards: ", extraCards);
            console.log("totalDecks: ", totalDecks);
            console.log("totalCards: ", (totalDecks * perDeck));

            // sort cards array to ascending on number
            for (let index = 0; index < totalDecks; index++) {
                cardsArr = [...cardsArr, ...cards]
            }
            cardsArr = cardsArr.sort((a, b) => a.number < b.number ? -1 : 1);

            if(extraCards > 0) {
                while (removed != extraCards) {
                    const cardIndex = cardsArr.findIndex((x) => x.point === 0);

                    if(cardIndex) {
                        cardsArr.splice(cardIndex, 1);
                        removed++;
                    }
                }
            }

            const partners = Math.floor(numberOfPlayers/2)
            const maxPoints = cardsArr?.filter((x) => x.point)?.map((x) => x.point)?.reduce((acc, next) => acc + next, 0);
            let minPoints = Math.ceil((maxPoints/2) + ((maxPoints/2) * (partners/numberOfPlayers)))
            if(minPoints % 5 !== 0) {
                minPoints -= (minPoints % 5);
            }

            resolve({cards: cardsArr, noOfDecks: totalDecks, minPoints, maxPoints})
        } catch (error) {
            reject(error)
        }
    })
}

// Create to lobby
export function createLobby(userId) {
    return new Promise((resolve, reject) => {
        try {
            const lobbyData = {
                userId: userId
            };

            apiRequest.post(`${API_URL}/lobbies/create`, lobbyData)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// Get lobby details
export function getLobby(lobbyId) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No lobbyId found");

            apiRequest.get(`${API_URL}/lobbies/${lobbyId}`)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// Update lobby details
export function updateLobby(lobbyId, updateObj) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No lobbyId found");

            apiRequest.patch(`${API_URL}/lobbies/${lobbyId}`, updateObj)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// Invite to lobby
export function inviteToLobby(lobbyId, sender, receiver) {
    return new Promise((resolve, reject) => {
        try {
            const {userName, email} = receiver;

            const inviteObject = {
                sender
            };
            if(userName) {
                // Invite using username
                inviteObject.receiver = {userName: userName};
            } else if(email) {
                // Invite using email
                inviteObject.receiver = {email: email};
            }

            apiRequest.post(`${API_URL}/lobbies/${lobbyId}/invite`, inviteObject)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// JOIN lobby
export function joinLobby(data) {
    return new Promise((resolve, reject) => {
        try {
            const {inviteId, lobbyCode, userId} = data
            if(!inviteId && !lobbyCode) return reject("No inviteId or lobbyCode found")

            if(lobbyCode && !userId) {
                return reject("No userId found")
            }

            let endpoint = "";
            const axiosData = {}
            if(inviteId) {
                endpoint = `/lobbies/invite/${inviteId}`
            } else {
                endpoint = `/lobbies/code/${lobbyCode}`
                axiosData.userId = userId;
            }
            apiRequest.post(endpoint, axiosData)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// REJECT lobby
export function rejectInvite(inviteId) {
    return new Promise((resolve, reject) => {
        try {
            if(!inviteId) return reject("No inviteId found")

            apiRequest.delete(`${API_URL}/lobbies/invite/${inviteId}`)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// LEAVE LOBBY
export function leaveLobby(lobbyId, userId) {
    return new Promise((resolve, reject) => {
        try {
            if(!lobbyId) return reject("No Lobby Id found");
            if(!userId) return reject("No userId found");

            apiRequest.patch(`/lobbies/${lobbyId}/leave`, {userId})
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

