<template>
    <div class="position-re">
        <button class="position-ab btn btn-primary border-right-radius-0" style="right: -15px;" @click="requestsVisible=true">&lt;</button>

        <div class="d-flex align-items-center">
            <button class="btn btn-primary mr-5px" @click="createLobbyFun()">
                Create Lobby
            </button>
            <button class="btn outline-primary" @click="showJoinModal = true">
                Join By Code
            </button>
        </div>

        <!-- FRIEND REQUESTS -->
        <Sidebar
            v-model:visible="requestsVisible"
            title="Friends"
            :options="[...requests, ...friends]"
            offset="15px"
            top="0px"
            width="450px"
            @update:visible="(event) => !event ? resetRequestData() : ''"
        >
            <template #body>
                <div class="overflow-y-auto" style="height: calc(100% - 98px);">
                    <div class="border-bottom">
                        <div class="p-15px">INVITATIONS</div>
                        <div
                            v-for="item in invitations"
                            :key="item._id"
                            class="d-flex align-items-center justify-content-between"
                            style="padding: 10px 15px;"
                        >
                            <span class="text-capitalize">{{friendData(item.sender)?.userName}}</span>

                            <div class="d-flex align-items-center">
                                <button
                                    class="btn btn-primary mr-5px"
                                    @click="joinLobbyFun(item?._id)"
                                >
                                    Join
                                </button>
                                <button
                                    class="btn outline-secondary"
                                    @click="rejectInviteFun(item?._id)"
                                >
                                    Reject
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="border-bottom">
                        <div class="p-15px">REQUESTS</div>
                        <div
                            v-for="item in requests"
                            :key="item._id"
                            class="d-flex align-items-center justify-content-between"
                            style="padding: 10px 15px;"
                        >
                            <span class="text-capitalize">{{friendData(item.sender)?.userName || item.sender}}</span>

                            <div class="d-flex align-items-center">
                                <button
                                    class="btn btn-primary mr-5px"
                                    @click="acceptRequestFun(item?._id)"
                                >
                                    Accept
                                </button>
                                <button
                                    class="btn outline-secondary"
                                    @click="rejectRequestFun(item?._id)"
                                >
                                    Reject
                                </button>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div class="p-15px">FRIENDS</div>
                        <div
                            v-for="item in userData?.friends"
                            :key="item._id"
                            class="d-flex align-items-center justify-content-between"
                            style="padding: 10px 15px;"
                        >
                            <span class="text-capitalize">{{item}}</span>
                        </div>
                    </div>
                </div>
                <div class="border-top p-15px" style="width: calc(100% - 30px);">
                    <div class="pb-10px">
                        <label for="username">Send Friend Request</label>
                    </div>
                    <div class="d-flex position-re">
                        <input
                            placeholder="Enter Username"
                            type="text"
                            id="username"
                            class="form-control"
                            v-model.trim="formData.userName.value"
                            @keypress.enter="sendFriendRequestFun()"
                            @keyup="checkErrors({
                                'field': formData.userName,
                                'name': formData.userName.name,
                                'validations': formData.userName.rules,
                                'type': formData.userName.type,
                                'event': $event
                            })"

                            :style="`${formData.userName.error ? 'outline:1px solid red; color: red;' : ''}`"
                        >
                        <span class="invalid-feedback red" v-if="formData.userName.error">{{formData.userName.error}}</span>
                        <button class="btn outline-primary" @click="sendFriendRequestFun()">Request</button>
                    </div>
                </div>
            </template>
        </Sidebar>

        <!-- JOIN BY LOBBY CODE MODAL -->
        <Modal
            v-model="showJoinModal"
            title="Join by lobby code"
            acceptButtonText="Join"
            @accept="joinLobbyByCodeFun()"
            @close="resetValidation(), showJoinModal = false"
        >
            <template #body>
                <div class="d-flex flex-column position-re m-1">
                    <input
                        type="text"
                        v-model="lobbyCode.value"
                        class="form-control"
                        placeholder="Enter lobby code"
                        style="width: 300px;"
                        @keyup="checkErrors({
                            'field': lobbyCode,
                            'name': lobbyCode.name,
                            'validations': lobbyCode.rules,
                            'type': lobbyCode.type,
                            'event': $event
                        })"
                    >
                    <span class="invalid-feedback red" v-if="lobbyCode.error">{{lobbyCode.error}}</span>
                </div>
            </template>
        </Modal>
    </div>
</template>

<script setup>
// PACKAGES
import { createLobby, joinLobby, rejectInvite } from "@/helpers/lobby";
import { computed, defineComponent, inject, nextTick, ref } from "vue";
import { useRouter } from "vue-router";
import { useToast } from "vue-toast-notification";
import { useStore } from "vuex";
import { useValidation } from "@/utils/composables/validation";
import {acceptRequest, createRequest, deleteRequest} from "@/helpers/friendRequests"

// COMPONENTS
import Sidebar from "@/components/molecule/Sidebar/Sidebar.vue"
import Modal from "@/components/atom/Modal/Modal.vue"

// HELPERS
const userId = inject("userId");
const userData = inject("userData");
const $toast = useToast();
const router = useRouter();
const {getters} = useStore();
const {checkErrors, checkAllFields} = useValidation()

defineComponent({
    name: "Home-Component"
})

const lobbyCode = ref({
    value: "",
    rules: "required | min: 6 | max: 6",
    name: "lobby code",
    error: "",
});
const requestsVisible = ref(false);
const requestInProgress = ref(false);
const showJoinModal = ref(false);
const formData = ref({
    userName: {
        value: "",
        rules: "required | regex: ^[A-Za-z0-9]*$",
        name: "userName",
        error: "",
    }
});
const invitations = computed(() => {
    return getters['users/invites'];
});
const requests = computed(() => {
    return getters['users/requests'];
});
const friends = computed(() => {
    return getters['users/friends'];
});

function createLobbyFun() {
    createLobby(userId.value)
    .then((res) => {
        $toast.success("Lobby created successfully", {position: "top-right"});
        setTimeout(() => {
            router.push({name: "Lobby", params: {id: res.data?.data?._id}})
        });
    })
    .catch((error) => {
        console.error("ERROR in create lobby: ", error);
        $toast.error("Something went wrong", {position: "top-right"});
    })
}

function joinLobbyFun(inviteId) {
    joinLobby({inviteId})
    .then((res) => res.data)
    .then((res) => {
        if(res.status) {
            setTimeout(() => {
                router.push({name: "Lobby", params: {id: res?.data?.lobbyId}})
            });
            $toast.success("Joined lobby", {position: "top-right"});
        } else {
            $toast.error(res?.statusText, {position: "top-right"});
        }
    })
    .catch((error) => {
        console.error("ERROR in accept invite: ", error.response?.data?.statusText || error);
        $toast.error(error.response?.data?.statusText || "Something went wrong", {position: "top-right"});
    })
}

function joinLobbyByCodeFun() {
    checkAllFields({lobbyCode: lobbyCode.value})
    .then((valid) => {
        if(valid) {
            showJoinModal.value = false;
            joinLobby({lobbyCode: lobbyCode.value.value, userId: userId.value})
            .then((res) => res.data)
            .then((res) => {
                if(res.status) {
                    setTimeout(() => {
                        router.push({name: "Lobby", params: {id: res?.data?._id}})
                    });
                    $toast.success("Joined lobby", {position: "top-right"});
                } else {
                    $toast.error(res?.statusText, {position: "top-right"});
                }
            })
            .catch((error) => {
                console.error("ERROR in join by lobby code: ", error.response?.data?.statusText || error);
                $toast.error(error.response?.data?.statusText || "Something went wrong", {position: "top-right"});
            })

            lobbyCode.value.value = '';
        }
    })
    .catch((error) => {
        console.error("ERROR in validation: ", error);
    })
}

function rejectInviteFun(inviteId) {
    rejectInvite(inviteId)
    .then((res) => res.data)
    .then((res) => {
        if(!res.status) {
            $toast.error(res?.statusText, {position: "top-right"});
        } else {
            $toast.success("Invitation rejected", {position: "top-right"});
        }
    })
    .catch((error) => {
        console.error("ERROR in reject invite: ", error.response?.data?.statusText || error);
        $toast.error(error.response?.data?.statusText || "Something went wrong", {position: "top-right"});
    })
}

function resetRequestData() {
    nextTick(() => {
        formData.value.userName.value = ""
        formData.value.userName.error = ""
    })
}
function sendFriendRequestFun() {
    try {
        checkAllFields(formData.value)
        .then((valid) => {
            if(valid) {
                try {
                    if(userData.value.userName === formData.value.userName.value) {
                        $toast.error("Cannot send request to yourself", {position: "top-right"});
                        return;
                    }
                    if(requestInProgress.value) {
                        return console.log("Request already in process");
                    }
                    requestInProgress.value = true;

                    createRequest({
                        sender: userId.value,
                        receiver: {
                            userName: formData.value.userName.value
                        }
                    })
                    .then((res) => res.data)
                    .then((res) => {
                        if(res.status) {
                            $toast.success("Request sent successfully", {position: "top-right"})
                        } else {
                            $toast.error(res.statusText, {position: "top-right"})
                        }
                        requestInProgress.value = false;
                        resetRequestData();
                    })
                    .catch((error) => {
                        resetRequestData();
                        requestInProgress.value = false;
                        console.error("ERROR in create friend request: ", error);
                    })
                } catch (error) {
                    resetRequestData();
                    requestInProgress.value = false;
                    console.error("ERROR in send friend request: ", error);
                }
            }
        })
        .catch((error) => {
            console.error("ERROR: ", error);
        })
    } catch (error) {
        console.error("ERROR in send friend request: ", error);
    }
}

function acceptRequestFun(id) {
    if(requestInProgress.value) {
        return console.log("Request already in process");
    }
    requestInProgress.value = true;
    acceptRequest(id)
    .then((res) => res.data)
    .then((res) => {
        requestInProgress.value = false;
        if(res.status) {
            $toast.success("Request accepted", {position: "top-right"})
        } else {
            $toast.error(res.statusText, {position: "top-right"})
        }
    })
    .catch((error) => {
        requestInProgress.value = false;
        console.error("ERROR in reject request: ", error);
    })
}

function rejectRequestFun(id) {
    if(requestInProgress.value) {
        return console.log("Request already in process");
    }
    requestInProgress.value = true;
    deleteRequest(id)
    .then((res) => res.data)
    .then((res) => {
        requestInProgress.value = false;
        if(res.status) {
            $toast.success("Request rejected", {position: "top-right"})
        } else {
            $toast.error(res.statusText, {position: "top-right"})
        }
    })
    .catch((error) => {
        requestInProgress.value = false;
        console.error("ERROR in reject request: ", error);
    })
}

function friendData(friendId) {
    return friends.value?.find((x) => x._id === friendId);
}

function resetValidation() {
    lobbyCode.value.value = "";
    lobbyCode.value.error = "";
}
</script>

<style lang="css" scoped>

</style>