import { apiRequest } from "@/config/axios";

export function createRequest(data = {}) {
    return new Promise((resolve, reject) => {
        try {
            apiRequest.post(`/requests/create`, data)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// GET user friend requests
export function getInvites(userId) {
    return new Promise((resolve, reject) => {
        try {
            apiRequest.get(`/requests/${userId}`)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}

// UPDATE
export function acceptRequest(requestId) {
    return new Promise((resolve, reject) => {
        try {
            try {
                if(!requestId) return reject("No requestId found");

                apiRequest.patch(`/requests/${requestId}/accept`)
                .then((res) => {
                    resolve(res)
                })
                .catch((error) => {
                    reject(error)
                })
            } catch (error) {
                reject(error);
            }
        } catch (error) {
            reject({status: false, statusText: error?.message});
        }
    })
}

// DELETE
export function deleteRequest(requestId) {
    return new Promise((resolve, reject) => {
        try {
            if(!requestId) return reject("No requestId found");

            apiRequest.delete(`/requests/${requestId}`)
            .then((res) => {
                resolve(res)
            })
            .catch((error) => {
                reject(error)
            })
        } catch (error) {
            reject(error);
        }
    })
}